	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Miles per Dollar","date":"22 Aug 2005","slug":"/2005-08-22-miles-per-dollar/","tags":"Y2005"},"html":"<p><a href=\"http://davespicks.com/archive/2005/08/22.html#1124627674\" title=\"22. August, 2005 - older now\">Dave's Picks:</a></p>\n<blockquote><p>The Fred Krause Miles Per Gallon is defined as the mileage obtained by a vehicle traveling 85 miles per hour with the air conditioner set on 'MAX.' My Camaro got nearly 35 FKMPG. My Blazer? Closer to 20. An even more useful measure is MPD (miles ger dollar), which turns it all into dollars. Until recently, I was getting a fairly solid 10 MPD, but the uptick in gas prices has wrecked that.</p></blockquote>\n<p>Hmmm, my <a href=\"http://www.papascott.de/archives/2004/08/21/used-car/\">Lupo</a> gets 5 l/100 km at 0.99&euro; per l of biodiesel, which at $1.20 per Euro works out to exactly 10 MPD. So I can't complain!</p>"},"next":{"fields":{"title":"Playmobil Lego Alliance","date":"20 Aug 2005","slug":"/2005-08-20-playmobil-lego-alliance/"}},"previous":{"fields":{"title":"Bonjour!","date":"23 Aug 2005","slug":"/2005-08-23-bonjour/"}}},{"node":{"fields":{"title":"Playmobil Lego Alliance","date":"20 Aug 2005","slug":"/2005-08-20-playmobil-lego-alliance/","tags":"Y2005"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/35584210/\" title=\"photo sharing\"><img src=\"http://photos28.flickr.com/35584210_78f7984635_m.jpg\" alt=\"\" style=\"border: solid 0px #000000;\" /></a></p>\n<p>Woe is us, our enemies have banded together.</p>\n<p>In the background, the remains of O'Hare Airport....</p>"},"next":{"fields":{"title":"Backyard Camping","date":"20 Aug 2005","slug":"/2005-08-20-backyard-camping/"}},"previous":{"fields":{"title":"Miles per Dollar","date":"22 Aug 2005","slug":"/2005-08-22-miles-per-dollar/"}}},{"node":{"fields":{"title":"Backyard Camping","date":"20 Aug 2005","slug":"/2005-08-20-backyard-camping/","tags":"Y2005"},"html":"<p>Karl went <a href=\"http://chicagokarl.de/2005/08/20/backyard-camping\" title=\"ChicagoKarl &raquo; Blog Archive &raquo; backyard camping\">backyard camping</a> with his sons last night. On vacation Christopher slept in a pickup bed with his grandmother, does that count?</p>"},"next":{"fields":{"title":"Joey's Three Principles of Money","date":"13 Aug 2005","slug":"/2005-08-13-joeys-three-principles-of-money/"}},"previous":{"fields":{"title":"Playmobil Lego Alliance","date":"20 Aug 2005","slug":"/2005-08-20-playmobil-lego-alliance/"}}}],"pathPrefix":"","first":false,"last":false,"index":288,"pageCount":1101}}

